.nav {
	position: fixed;
	top: 50px;
	left: 10px;
	display: flex;
	flex-direction: column;
	background-color: white;
	padding: 10px 20px;
	box-shadow: 5px 5px 3px rgba(0, 0, 0, 0.15);
}

.nav a {
	text-decoration: none;
	color: #1e3ec0;
	text-transform: uppercase;
	font-size: 12px;
	text-align: center;
	font-weight: 600;
}

.nav a:not(:last-child) {
	margin-bottom: 20px;
}
